<template>
    <canvas width="1200" height="600" id="main"></canvas>

</template>

<script>
import * as echarts from 'echarts';
import Bus from '@/utils/bus';
export default {
    data() {
        return {
            volList: []
        }
    },
    mounted() {
        Bus.$on('volRemind-fillData', (res) => {
            this.volList = res.sort((x, y) => {
                return parseInt(x[1]) - parseInt(y[1])
            });

            this.fillChart();
        })
    },

    methods: {
        getAvg120Vol(volData, priceData) {
            let buyArray = [];
            let sellArray = [];
            let isSellList = [];
            // x
            let DAYS = 200;
            let MIN_DAYS = 10;
            let data = [];
            volData.forEach((item, index) => {
                // 0 1119 
                if (index < DAYS) {
                    let s = volData.slice(0, index).reduce(function (a, b) {
                        return a + b;
                    }, 0);
                    isSellList.push(0)
                    data.push(s / (index + 1));
                    return;
                }
                let sum = this.getSum(volData.slice(index - DAYS, index))
                data.push(sum / DAYS);
                // XX
                let sum5avg = this.getSum(volData.slice(index - MIN_DAYS, index));
                sum5avg = sum5avg / MIN_DAYS;
                // x70%x50%
                if (sum * 1.3 < sum5avg * DAYS && priceData[index] * 2 > (priceData[index - 1] + priceData[index - 2])) {
                    isSellList.push(1)
                    sellArray.push(priceData[index]);
                } else if (sum * 0.6 > sum5avg * DAYS) {
                    isSellList.push(-1);
                    buyArray.push(priceData[index])
                } else {
                    isSellList.push(0);
                }

            })
            console.log("");
            console.log(this.getSum(sellArray)/sellArray.length);
            console.log("");
            console.log(this.getSum(buyArray)/buyArray.length);
            return isSellList;
        },
        getSum(arr) {
            let sum  =  arr.reduce(function (a, b) {
                return a + b;
            }, 0);
            return sum;
        },
        analyseVolData() {
            // "ts_code,trade_date,close,vol,amount"
            // 
            let xAxisData = this.volList.map(item => {
                return item[1]
            });
            // 
            let volData = this.volList.map(item => {
                return item[3]
            });
            // 
            let amountData = this.volList.map(item => {
                return item[4]
            });
            // 
            let priceData = this.volList.map(item => {
                return item[2]
            });
            return {
                xAxisData,
                volData,
                amountData,
                priceData
            }
        },
        fillChart() {
            let {
                xAxisData,
                volData,
                amountData,
                priceData
            } = this.analyseVolData();
            amountData = this.getAvg120Vol(volData, priceData);
            volData = volData.slice(120)
            xAxisData = xAxisData.slice(120)
            amountData = amountData.slice(120)
            console.log("amountData");
            console.log(amountData);
            priceData = priceData.slice(120)

            if (!xAxisData || xAxisData && xAxisData.length <= 0) {
                return
            }
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            myChart.clear();
            var colors = ['#5470C6', '#91CC75', '#EE6666'];

            let option = {
                color: colors,

                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross'
                    }
                },
                grid: {
                    right: '20%'
                },
                toolbox: {
                    feature: {
                        dataView: { show: true, readOnly: false },
                        restore: { show: true },
                        saveAsImage: { show: true }
                    }
                },
                dataZoom: [{
                    type: 'inside'
                }, {
                    type: 'slider'
                }],
                legend: {
                    data: ['', '', '']
                },
                xAxis: [
                    {
                        type: 'category',
                        axisTick: {
                            alignWithLabel: true
                        },
                        data: xAxisData
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        name: '',
                        // min: 0,
                        // max: 250,
                        position: 'right',
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: colors[0]
                            }
                        },
                        axisLabel: {
                            formatter: '{value}'
                        },
                    },
                    {
                        type: 'value',
                        name: '',
                        min: -2,
                        max: 2,
                        position: 'right',
                        offset: 80,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: colors[1]
                            }
                        },
                        axisLabel: {
                            formatter: '{value}'
                        }
                    },
                    {
                        type: 'value',
                        name: '',
                        // min: 0,
                        // max: 25,
                        position: 'left',
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: colors[2]
                            }
                        },
                        axisLabel: {
                            formatter: '{value}'
                        }
                    }
                ],
                series: [
                    {
                        name: '',
                        type: 'bar',
                        data: volData,
                        // markLine: {
                        //     data: [
                        //         { type: 'average', name: '' }
                        //     ]
                        // }
                    },
                    {
                        name: '',
                        type: 'line',
                        yAxisIndex: 1,
                        data: amountData,
                        markLine: {
                            data: [
                                { type: 'average', name: '' }
                            ]
                        }
                    },
                    {
                        name: '',
                        type: 'line',
                        yAxisIndex: 2,
                        data: priceData
                    }
                ]
            };
            console.log(option);
            option && myChart.setOption(option);
        }
    }
}
</script>

